/* ---------------- GLOBAL ---------------- */
html, body, #root, .App {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  scroll-behavior: smooth; /* smooth scroll */
  font-family: Arial, sans-serif;
  background-color: #121212;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  width: 100%;
  padding: 0 1rem;
}

.App-link {
  color: #61dafb;
}

@keyframes fadein {
  from {opacity: 0; transform: translateX(20px);}
  to {opacity: 1; transform: translateX(0);}  
}
.fade-in {
  opacity: 0;
  animation: fadein 1s forwards;
}
.delay1{animation-delay: 0s;}
.delay2{animation-delay: 0.5s;}
.delay3{animation-delay: 1s;}
.delay4{animation-delay: 1.5s;}

/* ---------------- NAVBAR ---------------- */
.Navbar {
  width: 100%;
  height: 9vh;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
  padding: 0.5rem 1rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  background-color: #1f1f1f;
  transition: max-height 0.3s ease-in-out;
}

.Navbar a {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

.Navbar a:hover {
  color: #61dafb;
}

/* HAMBURGER ICON */
.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 20px;
}

.hamburger span {
  height: 3px;
  width: 25px;
  background: white;
  margin: 4px 0;
  border-radius: 5px;
}
/* ---------------- PROJECT BUTTONS ---------------- */
.Home button, .delay4 {
  min-width: 100px;   /* smaller width */
  padding: 0.5rem 1rem; 
  margin: 0.5rem;     /* consistent spacing */
  font-size: 0.95rem; /* smaller text */
}


/* ---------------- HOME ---------------- */
.Home {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1200px;
  margin: 2rem auto;
  flex-wrap: wrap;
}

.Home button {
  min-width: 120px;
  padding: 0.8rem 1.2rem;
  margin: 0.5rem;
}

/* ---------------- INFO & PICTURE ---------------- */
.Info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 45%;
  min-width: 280px;
  color: #ffffff;
  margin-bottom: 1rem;
  transition: all 0.3s ease;
}

.Picture {
  width: 45%;
  min-width: 280px;
}

.Picture img {
  width: 100%;       /* fill parent width */
  height: auto;      /* maintain aspect ratio */
  max-height: 70vh;  /* prevent oversized image */
  object-fit: contain; /* prevent blur/stretch */
  border-radius: 4%;
  transition: all 0.3s ease;
}

.Picture img:hover {
  transform: scale(1.05);
  box-shadow: 0 0 10px 2px white;
}

/* ---------------- SKILLS & EDUCATION ---------------- */
.Skills, .Education {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 1rem;
  color: white;
  padding: 1rem;
}

.skill, .edu {
  flex: 1 1 250px;
  max-width: 400px;
  min-height: 130px;
  margin: 1rem;
  padding: 1.5rem;
  border-top: 2px solid #ff4d4d;
  border-left: 2px dashed white;
  border-radius: 12%;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill:hover, .edu:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 10px 20px silver;
}

/* ---------------- PROJECTS ---------------- */
.Projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 1rem;
  padding: 1rem;
  color: white;
}

.Project {
  flex: 1 1 280px;
  max-width: 450px;
  padding: 1rem;
  text-align: center;
  border-top: 2px solid #ff4d4d;
  border-left: 2px dashed white;
  border-radius: 12%;
  background-color: #121212;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.Project:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 10px 20px silver;
}

.Project_Image img {
  width: auto;
  height: 32vh;
  border-radius: 25%;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.Project_Image img:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px 2px silver;
}

/* ---------------- ABOUT ---------------- */
.About {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 2rem;
  width: 100%;
  color: white;
}

.Intro-about {
  flex: 1 1 300px;
  max-width: 600px;
  min-width: 250px;
  padding: 2rem;
  margin: 1rem;
  border-top: 2px solid #ff4d4d;
  border-left: 2px dashed white;
  border-radius: 12%;
  background-color: #121212;
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  font-size: 1rem;
}

.Intro-about:hover {
  transform: scale(1.05);
  box-shadow: 0 0 10px 2px silver;
}

/* ---------------- FOOTER ---------------- */
footer {
  position:fixed;
  bottom: 0;
  width: 100%;
  background-color: #121212;
  color: white;
  text-align: center;
  padding: 1rem;
  z-index: 1000;
}

/* ---------------- LINKS & BUTTONS ---------------- */
a:hover {
  color: silver;
  transform: scale(1.01);
  transition: 0.3s ease;
}

.delay4 {
  cursor: pointer;
  background-color: #77c5f3;
  color: black;
  border-radius: 10px;
  border: none;
  min-width: 120px;
  padding: 0.5rem 1rem;
  transition: all 0.3s ease;
}

.delay4 a { color: black; }

.delay4:hover, .delay4 a:hover {
  color: white;
  background-color: #121212;
  transform: scale(1.1);
}

/* ---------------- MEDIA QUERIES ---------------- */
@media (max-width: 1024px) {
  .Home {
    flex-direction: column;
  }
  .Info, .Picture {
    width: 90%;
  }
}

@media (max-width: 768px) {
  .Navbar {
    flex-direction: column;
    align-items: flex-start;
    max-height: 0;
    overflow: hidden;
    gap: 0.1rem;
    padding: 0.5rem;
      background-color: #121212;
  }
  .Navbar.active {
    max-height: 500px;
  }

  .hamburger {
    display: flex;
  }

  .Home button, .delay4 {
    width: 80%;
    font-size: 1rem;
  }

  .Project, .skill, .edu, .Intro-about {
    flex: 1 1 100%;
    max-width: 100%;
    margin: 0.5rem 0;
    padding: 1rem;
  }
  
  .Picture img {
    max-height: 50vh;
  }

  .About {
    flex-direction: column;
    align-items: center;
    padding: 1rem;
  }
}
